{
    let loadingDivEl = document.getElementById('loadingDivEl');
    let loadingTextEl = document.getElementById('loadingTextEl');
    loadingTextEl.innerText = "";
    setTimeout(function () {
        let loadingStyleInterval = setInterval(function () {
            if (loadingDivEl.style.opacity === undefined || loadingDivEl.style.opacity.length === 0) {
                loadingDivEl.style.opacity = '1'
            } else {
                let loadingOpacityValue = Number.parseFloat(loadingDivEl.style.opacity) - 0.1;
                loadingDivEl.style.opacity = (loadingOpacityValue).toString();
                if (loadingOpacityValue <= 0) {
                    clearInterval(loadingStyleInterval);
                    loadingDivEl.style.display = "none";
                }
            }
        }, 16);
    }, 1000);
}
// canvas
let canvasEL = document.getElementById("canvas-frame");
// *
let scene = initScene();
// *
let lightAmbient = initAmbientLight();
scene.add(lightAmbient);
let lightDirectional;
if (GlobalSetting.enableDirectionalLight) {
    lightDirectional = initDirectionalLight();
    scene.add(lightDirectional);
}

// *
let camera = initCamera();
camera.aspect = canvasEL.offsetWidth / canvasEL.offsetHeight;
camera.updateProjectionMatrix();
scene.add(camera);
// *
let renderer = initRenderer();
renderer.setSize(canvasEL.offsetWidth, canvasEL.offsetHeight);
renderer.setPixelRatio(window.devicePixelRatio);
document.getElementById('canvas-frame').appendChild(renderer.domElement);
// 
let objects = [];
// 
let controls = initControls("MCFirstPersonControl", scene, camera, renderer, objects);
// 
initBGMPlayer();
//
let lightOption = {
    current: 1, //
    low: 0.5,//
    high: 1,//
    becomeHigher: true//
};

//
function changeLight() {
    lightAmbient.intensity = getAmbientLightIntensity(lightOption.current, lightOption.high, lightOption.low);
    if (GlobalSetting.enableDirectionalLight) {
        lightDirectional.intensity = getDirectionalLightIntensity(lightOption.current, lightOption.high, lightOption.low);
    }
    if (lightOption.becomeHigher) {
        if (lightOption.current <= 1) {
            lightOption.current -= 0.0002;
        } else {
            lightOption.becomeHigher = false;
        }
    } else {
        if (lightOption.current >= 0.5) {
            lightOption.current += 0.0002;
        } else {
            lightOption.becomeHigher = true;
        }
    }
}

